import deDE from './snippet/de-DE';
import enGB from './snippet/en-GB';

import './page/faq-list';

Shopware.Module.register('faq', {
    type: 'plugin',
    name: 'Faq',
    // title: 'faq.general.mainMenuItemGeneral',
    // description: 'faq.general.descriptionTextModule',
    title: 'FAQ',
    description: 'FAQ',
    color: '#ff3d58',
    icon: 'default-shopping-paper-bag-product',

    snippets: {
        'de-DE': deDE,
        'en-GB': enGB
    },

    routes: { 
        list: {
            component: 'faq-list',
            path: 'list'
        },
        create: {
            component: 'faq-create',
            path: 'create',
            meta: {
                parentPath: 'faq.list'
            }
        },
        detail: {
            component: 'faq-detail',
            path: 'detail/:id',
            meta: {
                parentPath: 'faq.list'
            }
        }
     },

    navigation: [{
        parent: 'sw-extension',
        id: 'faq-list',
        label: 'faq.general.mainMenuItemGeneral',
        color: '#ff3d58',
        path: 'faq.list',
        icon: 'default-shopping-paper-bag-product',
        position: 100
    }]
});

